import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faHeart,
  faImage,
  faThumbsUp
} from '@fortawesome/free-solid-svg-icons';
import { faInstagram } from '@fortawesome/free-brands-svg-icons';
import Fade from './spring/Fade';
import Card from './spring/Card';
export default class Home extends Component {
  state = {
    isLoading: true,
    users: [],
    error: null
  };

  fetchUsers() {
    fetch(
      `https://api.unsplash.com/photos/?client_id=cf49c08b444ff4cb9e4d126b7e9f7513ba1ee58de7906e4360afc1a33d1bf4c0`
    )
      .then(response => response.json())
      .then(data =>
        this.setState({
          users: data,
          isLoading: false
        })
      )
      .catch(error => this.setState({ error, isLoading: false }));
  }

  componentDidMount() {
    this.fetchUsers();
  }
  render() {
    const { isLoading, users, error } = this.state;
    return (
      <div>
        {error ? <p>{error.message}</p> : null}
        {!isLoading ? (
          users.map(user => {
            const { id, urls } = user;
            return (
              <div className='container'>
                <div className='row mt-3'>
                  <div key={id} className='col-md-4 col-12 mb-5'>
                    <Card
                      src={urls.small}
                      alt=''
                      className='imageGallery w-100 rounded'
                    />
                  </div>
                  <div className='col-md-3 col-6 m-auto'>
                    <h3 className='text-center '>
                      Total <FontAwesomeIcon icon={faInstagram} />
                    </h3>

                    <h3 className='text-center p-4'>
                      <Fade icon={faHeart} className='m-2 text-danger' />
                      {user.user.total_likes}
                    </h3>
                    <h3 className='text-center'>
                      <FontAwesomeIcon icon={faImage} className='mr-3' />
                      {user.user.total_photos}
                    </h3>
                  </div>
                  <div className='ml-auto my-auto col-md-3 col-6'>
                    <h2 className='text-center'>{user.user.name}</h2>
                    <h3 className='text-center p-1'>
                      <Fade icon={faThumbsUp} className='m-2 text-primary' />
                      {user.likes}
                    </h3>
                    <p className=''>{user.user.bio}</p>
                  </div>
                </div>
              </div>
            );
          })
        ) : (
          <h3>Loading...</h3>
        )}
      </div>
    );
  }
}
